
















































import { Component, Vue, Watch } from 'vue-property-decorator'
import FilterFieldDialog from '@/views/Editor/FilterFieldDialog/index.vue'
import OptionsEditList from '@/views/components/OptionsEditList.vue'
import { FilterField } from '@laomao800/vue-listview'

@Component({
  components: {
    OptionsEditList,
    FilterFieldDialog
  }
})
export default class FilterbarFields extends Vue {
  public optionsData = []
  public dialogVisible = false
  public editIndex: number | null = null
  public model: {
    filterFields: FilterField[]
  } = {
    filterFields: [
      {
        type: 'text',
        model: 'text',
        label: '文本字段'
      },
      {
        type: 'select',
        model: 'select',
        label: '单选字段',
        options: [
          { label: '选项 1', value: 1 },
          { label: '选项 2', value: 2 },
          { label: '选项 3', value: 3 }
        ]
      },
      {
        type: 'multipleSelect',
        model: 'multipleSelect',
        label: '多选字段',
        options: [
          { label: '选项 1', value: 1 },
          { label: '选项 2', value: 2 },
          { label: '选项 3', value: 3 }
        ]
      },
      { type: 'date', model: 'date', label: '日期' },
      {
        type: 'dateRange',
        model: 'dateRange',
        label: '日期范围'
      }
    ]
  }

  get fields () {
    return this.model.filterFields
  }
  set fields (val) {
    this.model.filterFields = val
  }
  get editModel () {
    return this.editIndex !== null
      ? this.model.filterFields[this.editIndex]
      : null
  }

  @Watch('dialogVisible')
  onVisibleChanged (val: boolean) {
    if (!val) {
      this.editIndex = null
    }
  }

  save (data: FilterField) {
    if (this.editIndex) {
      // edit
      this.model.filterFields.splice(this.editIndex, 1, data)
      this.editIndex = null
    } else {
      // add
      this.fields.push(data)
    }
  }

  edit (index: number) {
    this.editIndex = index
    this.dialogVisible = true
  }

  remove (index: number) {
    this.fields.splice(index, 1)
  }

  getFieldTypeIcon (type: string) {
    switch (type) {
      case 'text':
        return 'el-icon-edit'

      case 'select':
      case 'multipleSelect':
      case 'cascader':
        return 'el-icon-tickets'

      case 'date':
      case 'dateRange':
      case 'dateTime':
      case 'dateTimeRange':
        return 'el-icon-date'

      case 'timeSelect':
      case 'timePicker':
      case 'timePickerRange':
        return 'el-icon-time'
    }
  }
}
